.candidate_card_container {
  width: 100%;
  height: 84px;
  background: #ffffff;
  border: 0.4px solid rgba(0, 0, 0, 0.15);
  box-shadow: -1px -1px 10px rgba(0, 0, 0, 0.02),
    1px 2px 32px rgba(0, 0, 0, 0.02);
  border-radius: 6px;
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 1rem;
  gap: 1rem;
}
.candidate_card_container > img {
  width: 52px;
  height: 52px;
  border-radius: 50%;
}
.candidate_card_container_text {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.candidate_card_row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.candidate_card_row > h6 {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #121212;
  margin-bottom: 0.2rem;
}
.candidate_card_row > p {
  font-weight: 400;
  font-size: 11px;
  line-height: 13px;
  color: rgba(18, 18, 18, 0.5);
  margin: 0;
  margin-bottom: 0.3rem;
}
